/* =============================
==========footer============= */

/* FooterStyle.css */

/* Style for footer links */
.footer-link {
  color: #ffffff;
  text-decoration: none;
  margin: 0 10px;
}

/* Style for the divider between footer links */
.footer-divider {
  color: #ffffff;
  margin: 0 5px;
}

 .footer {
  height: 140px;
  color: #ffc400;
  padding: 25px;
}

.footer a {
  text-decoration: none;
  color: #ffc400;
  padding: 10px;
}

.footer a:hover {
  color: #ffefba;
  border-bottom: 1px solid #ffefba;
}

/* =================================
======== page not found css ======= */
.pnf {
  display: flex;
  min-height: 65vh;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.pnf-title {
  font-size: 100px;
  font-weight: 700;
}

.pnf-heading {
  font-weight: normal;
}

.pnf-btn {
  color: black;
  border: 1px solid black;
  text-decoration: none;
  padding: 10px;
  margin-top: 10px;
}
.pnf-btn:hover {
  background-color: black;
  color: white;
}

